const story1 = [
  'He pasado muchos momentos felices allí, además de estar con las personas que me han criado, es un un lugar donde disfruté como nunca, donde aprendí y reforzé el vínculo con las personas a las que quiero.',
  'Un lugar muy cálido, con piscina, campos de "ametllers", un camino donde recolectabas moras cuando te estabas muriendo de hambre.',
  'https://media4.giphy.com/media/dz2otp42VWrpnMXsqX/giphy.gif',
];

const story2 = [
  'La casa de mis abuelos, porque es donde más tiempo pasé Bonita y nostálgica. A veces un poco triste. Por lo general muy dulce porque es como recuerdo estar allí, aunque me genera tristeza la lejanía.',
  'Muebles de madera. Gotelé. Muchas vírgenes y Cristos. Una lámpara de araña. Juguetes. Navidad. Canal Extremadura por la tardes. El tabaco de mi abuelo. Coser. La tele siempre puesta. El balcón y los atardeceres.',
  'https://media3.giphy.com/media/Uz32750g3o84j6z8W9/giphy.gif',
];

const story3 = [
  'Recuerdo muchas anécdotas de ese sitio. Mi primer st joan con amigxs del cole fue allí, allí pasé con una de mis primeras parejas de la adolescencia, jugaba, era un punto de quedada con amigxs y he visto cómo ha cambiado con los años ya que suelo pasar delante.',
  'Tiene una fuente para beber agua con una figura de una oca encima, al lado tiene un parque infantil con unos columpios, tobogan y creo que un balancín? enfrente hay bares y negocios del barrio',
  'https://media2.giphy.com/media/s0HvjhGqcjaGYRSdFR/giphy.gif',
];

const story4 = [
  'Me hace pensar en cómo era de pequeño como soy y como debería de ser de manera natural, y nostalgia',
  'Desde la infancia recuerdo las gradas más grandes de lo que son, las barras de azul oscuro brillante, grande como un coliseo,casi siempre lo recuerdo húmedo seguramente por el tipo de material que era, una fuente circular con los pitorros oxidados el suelo de la cancha desgastado pero no te podías resbalar en el ni queriendo , piedritas por el suelo y solitario, siempre vacío como si sólo fuera una zona de paso, de hecho solo transitaba las gradas para bajar dando saltos o subir a zancadas.',
];

const stories = () => {
  return (
    <div
      style={{
        display: 'flex',
        justifyContent: 'space-around',
        width: '100vw',
      }}
    >
      <div
        style={{
          width: '50vw',
          height: '80vh',
          fontSize: 22,
          display: 'block',
          textAlign: 'justify',
        }}
      >
        <div style={{ marginLeft: 0 }}>
          <h2> Vens o tornes?</h2>
          <div style={{ margin: 30 }}>
            Un projecte que ens parla de les relacions entre les persones i
            l’espai en el que han passat la seva infància. Fent que l'espectador
            recordi els llocs i les sensacions d’aquests espais, l’obra es
            materialitza en forma d’una web interactiva que termina portant a
            cada persona a recòrrer la ubicació de la que ens parla a través
            d’internet.
          </div>
        </div>
        <div style={{ position: 'absolute', top: 350 }}>
          <Story first={story1[0]} second={story1[1]} url={story1[2]}></Story>
        </div>
      </div>
      <div>
        <div style={{ position: 'absolute', left: 1400 }}>
          <Story first={story2[0]} second={story2[1]} url={story2[2]}></Story>
        </div>
        <div style={{ position: 'absolute', top: 600, left: 1200 }}>
          <Story
            width={600}
            first={story3[0]}
            second={story3[1]}
            url={story3[2]}
          ></Story>
        </div>
      </div>
    </div>
  );
};

const Story = ({ first = '', second = '', url = '', width }) => {
  return (
    <div
      className={'box'}
      style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',

        border: 'solid 1px gainsboro',
      }}
    >
      <img src={url} style={{ height: 349, width: 480 }}></img>
      <div
        style={{
          width: 280,
          padding: 20,
        }}
      >
        <div style={{ textAlign: 'justify', fontSize: 20 }}>{first}</div>
        <br />
        <div style={{ textAlign: 'justify', fontSize: 20 }}>{second}</div>
      </div>
    </div>
  );
};
export default stories;
