import logo from './logo.svg';
import './App.css';
import React from 'react';
import Autosuggest from 'react-autosuggest';
import { SearchBox } from '@loadup/react-google-places-autocomplete';

const suggestions = [
  {
    text: 'Apple',
  },
  {
    text: 'Banana',
  },
  {
    text: 'Cherry',
  },
  {
    text: 'Grapefruit',
  },
  {
    text: 'Lemon',
  },
];

const renderSuggestion = (suggestion) => <div>{suggestion.text}</div>;
function Travel({ question, setLocation }) {
  const [userInput, setUserInput] = React.useState(null);

  return (
    <div className="Slide fadein">
      <div>
        <div className="slide-question ">
          Introdueix aquí la direcció del lloc:
        </div>
      </div>
      <br />
      <br />
      <SearchBox
        autoFocus
        id="example-searchbox-id"
        style={{ outline: 'none' }}
        placeholder={'Tornar a ...'}
        onPlaceChanged={({ original, parsed }) => {
          setLocation(
            `${original[0].geometry.location.lat()}, ${original[0].geometry.location.lng()} `
          );
        }}
      />
    </div>
  );
}

export default Travel;
