import { StreetView } from 'react-google-map-street-view';

const Street = ({ location, phrases, onNext }) => {
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <StreetView
        address={location.trim()}
        APIkey="AIzaSyCl33vSM61DKpACUlT1mO5ASGd2FkFOOgo"
        streetView
        zoomLevel={15}
      />
      <button
        className={`next-button bot-left-button`}
        onClick={() => onNext()}
        style={{ fontFamily: 'inherit !important' }}
      >
        Resum
      </button>
    </div>
  );
};

export default Street;
