import logo from './logo.svg';
import './App.css';

function Home({ onStart }) {
  return (
    <div className="Home fadein">
      <div className="home-title">Vens o tornes?</div>
      <br />
      <div className="home-desc-cont" style={{ width: 560 }}>
        <div className="home-desc">
          És un projecte interactiu per explorar els records i les vivències als
          llocs de la nostra infància.
          <br />
          <br />
          Respon les preguntes per arribar al final.
        </div>
      </div>

      <button
        onClick={onStart}
        className="start-button"
        style={{ fontFamily: 'inherit !important' }}
      >
        Començar
      </button>
    </div>
  );
}

export default Home;
