import logo from './logo.svg';
import './App.css';
import React from 'react';

function Slide({ question, onNext }) {
  const [userInput, setUserInput] = React.useState(null);
  return (
    <div className="Slide fadein">
      <div>
        <div className="slide-question ">{question}</div>
      </div>
      <br />
      <br />
      <textarea
        autoFocus
        key={question}
        onChange={(e) => setUserInput(e.target.value)}
        placeholder={'...'}
        style={{}}
        type=""
      ></textarea>
      <div>
        <button
          style={{
            visibility: userInput ? 'visible' : 'hidden',
            fontFamily: 'inherit !important',
          }}
          className={`next-button bot-left-button ${
            userInput ? 'fadein' : 'fadeof'
          }`}
          onClick={() => onNext(userInput)}
        >
          Següent
        </button>
      </div>
    </div>
  );
}

export default Slide;
